.header-wrapper{
    width: 100vw;
    display: flex;
    height: 50px;
    background-color: aliceblue;
    align-items: center;
    justify-content: space-around;
    margin: 0px 0 40px 0;
}
.header-wrapper li{
    font-size: 25px;
    list-style: none;
    font-weight: 600;
}
.header-wrapper li a{
    text-decoration: none;
    text-transform: uppercase;
    text-shadow: 5px 3px #00000026;
    color: currentColor;
}